.section {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    padding: 20px;
    max-width: 400px;
    width: 100%;
    border-radius: 15px;
    box-shadow: 0 0 35px 0 #000000;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    margin: 0 0 20px 0;
    font-size: 36px;
}

.auth {
    margin: 0 0 20px 0;
    font-size: 24px;
}

.authError {
    margin: 0 0 20px 0;
    font-size: 18px;
    text-align: center;
    color: rgb(193, 105, 105);
}

.inputName {
    margin: 0 0 10px 0;
    width: 300px;
    font-size: 18px;
}

.input {
    margin: 0 0 30px 0;
    padding: 10px;
    width: 300px;
    height: 40px;
    border: none;
    color: #000000;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    font-size: 16px;
}

.input:focus {
    overflow: hidden;
    outline: 1px solid #aeaeae;
}

.button {
    width: 300px;
    height: 60px;
}